	module.exports = {"data":{"post":{"html":"<p>The past few weeks I've noticed sporadic outages in the database for PapaScott. A few times a week the database, and thus the blog,  has been slow and unavailable for a few minutes. Since <a href=\"http://www.hetzner.de/\">Hetzner</a>, a hoster in Germany that has a very good reputation, is having a <a href=\"http://www.hetzner.de/news.html\">Christmas special</a>, I decided to move PapaScott there (onto simple webspace, not a dedicated server) from its old home in California and see if things are any better. We've been there since yesterday morning, and except for funny characters in one of the sidebar ads, so far so good.</p>","fields":{"title":"Back In Old Europe","date":"16 Dec 2006","url":"https://papascott.de/2006-12-16-back-in-old-europe/"}}},"pathContext":{"slug":"/2006-12-16-back-in-old-europe/","prev":{"fields":{"title":"Dragon Babies","date":"14 Dec 2006","slug":"/2006-12-14-dragon-babies/"}},"next":{"fields":{"title":"All About Us","date":"17 Dec 2006","slug":"/2006-12-17-all-about-us/"}}}}

